.App {
  background: -webkit-gradient(linear, left bottom, left top, from(rgb(0, 192, 255)), to(rgb(255, 192, 255)));
  background: -webkit-linear-gradient(bottom, rgb(0, 192, 255), rgb(255, 192, 255));
  background: -o-linear-gradient(bottom, rgb(0, 192, 255), rgb(255, 192, 255));
  background: linear-gradient(to top, rgb(0, 192, 255), rgb(255, 192, 255));
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  font-family: 'Arimo', sans-serif;
}

#title {
  font-family: 'Bungee Shade', cursive;
  font-size: 56px;
  color: white;
}

#credits, #cheatsheet-link {
  color: white;
}

#credits a, #cheatsheet-link a {
  text-decoration: none;
  color: white;
}

#credits a:hover, #cheatsheet-link a:hover {
  color: purple;
}

#output-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-item-align: center;
      align-self: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: 5px solid white;
  border-radius: 0.3em;
  background-color: white;
  margin-bottom: 80px;
  -webkit-box-shadow: 5px 5px 10px grey;
          box-shadow: 5px 5px 10px grey;  
  padding: 30px;
}

#preview {
  width: 60vw;
  text-align: left;
}

#editor {
margin: 40px 0px 30px 0px;
border: 2px solid white;
border-radius: 0.3em;
-webkit-box-shadow: 5px 5px 10px grey;
        box-shadow: 5px 5px 10px grey;      
padding: 5px;
}




